import {useState} from "react";
import Modal from "./modal";
import EmrehrForm from '../ProfileBuilder/ResumeBuilder/emrehr';

export default (props) => {
    const [showModal, setShowModal] = useState(false);
    const editEmrEhr = (event) => {
        event.preventDefault();
        setShowModal(!showModal);
        if (showModal) {
            props.cancel('resumeBuilder');
        }
    };
    const saveEmrEhr = (event) => {
        event.preventDefault();
        if (props.saveContinue('resumeBuilder', false)) {
            setShowModal(!showModal);
        }
    };
    return (
        <div className="col-md-12 col-xl-12">
            <div className="card">
                {
                    showModal &&
                    <Modal showHide={editEmrEhr} saveFunction={saveEmrEhr} label={"EMR/EHR System Experience"}>
                        <EmrehrForm/>
                    </Modal>
                }
                <div className="col-lg-12 col-md-12">
                    <div className="row">
                        <div className="card-header">
                            <div className="col-lg-4 col-md-4">
                                <b><h3>EMR/EHR System Experience</h3></b>
                            </div>
                            <div className="col-lg-2 col-md-2">
                                <a id="EditEHREMR" href="#" onClick={editEmrEhr}>
                                    <i className="fe fe-edit" title="Edit EMR/EHR System"></i>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="card-body">
                    <div className="form-group">
                        {
                            props.primary.map(prim => (
                                <div className="col-lg-12 col-md-12">
                                    <label htmlFor="exampleInputname1">{prim.name}</label>
                                </div>
                            ))
                        }
                    </div>
                    <hr className="Line"/>
                    <br/>
                    <div className="row">
                        <div className="col-lg-4 col-md-4">
                            <h3>Other EMR/EHR System </h3>
                        </div>
                        <div className="col-lg-2 col-md-2">
                            <a id="EditCertification" href="#" onClick={editEmrEhr}>
                                <i className="fe fe-edit" title="Edit Certification Form"></i>
                            </a>
                        </div>
                    </div>
                    <div className="form-group">
                        {
                            props.others.map(other => (
                                <div className="col-lg-4 col-md-4">
                                    <label htmlFor="exampleInputname1">{other}</label>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}
