import {gotoNextTab, gotoPreviousTab, setError, resetError, setCurrentTab} from "./slice";

export function nextTab() {
    return async dispatch => {
        dispatch(gotoNextTab());
    };
}

export function previousTab() {
    return async dispatch => {
        dispatch(gotoPreviousTab());
    };
}

export function profileError(error) {
    return async dispatch => {
        dispatch(setError(error));
    };
}

export function resetProfileError() {
    return async dispatch => {
        dispatch(resetError());
    };
}

export function gotoTab(tab) {
    return async dispatch => {
        dispatch(setCurrentTab(tab));
    };
}
