import {useState} from "react";
import Modal from "./modal";
import CovidForm from "../ProfileBuilder/Preference/covidForm";

export default (props) => {
    const [showModal, setShowModal] = useState(false);
    const editCovid = (event) => {
        event.preventDefault();
        setShowModal(!showModal);
        if (showModal) {
            props.cancel('preference');
        }
    };
    const saveCovid = (event) => {
        event.preventDefault();
        if (props.saveContinue('preference', false)) {
            setShowModal(!showModal);
        }
    };
    return (
        <div className="col-md-12 col-xl-12">
            <div className="card">
                {
                    showModal &&
                    <Modal label={"Are you Covid 19 vaccinated ?"} showHide={editCovid} saveFunction={saveCovid}>
                        <CovidForm errors={props.errors}/>
                    </Modal>
                }
                <div className="col-lg-12 col-md-12">
                    <div className="row">
                        <div className="card-header">
                            <div className="col-lg-5 col-md-5">
                                <h3>Are you COVID-19 vaccinated?</h3>
                            </div>
                            <div className="col-lg-2 col-md-2">
                                <a id="EditCovid" href="#" onClick={editCovid}>
                                    <i className="fe fe-edit" title="Edit Covid 19 vaccinated"></i>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="card-body">
                    <div className="form-group">
                        <div className="row">
                            <div className="col-lg-12 col-md-12">
                                <label htmlFor="exampleInputname">{props.vaccination}</label>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
